}

.App {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  position: relative;
}

.loadAll {
  position: fixed;
  z-index: 1000;
  width: 100vw;
  height: 100vh;
  color: #e7e7eb;
  background: #100e1d6c;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
