}
.text-weather {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.text-weather h1 {
  font-size: 8rem;
  font-weight: 500;
  color: #e7e7eb;
