.text-date .location {
  margin: 0.65rem 0px 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.125rem;
  font-weight: 600;
