/* modal search */
.modal-search {
  position: fixed;
  z-index: 500;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding: 0px 2rem;
  background: #1e213a;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: slideIn;
          animation-name: slideIn;
}
@media screen and (min-width: 1800px) and (max-width: 5000px) {
  .modal-search {
    padding: 0px 20%;
  }
}

@-webkit-keyframes slideIn {
  0% {
    right: 100%;
    opacity: 0;
  }
  100% {
    right: 0%;
    opacity: 1;
  }
}

@keyframes slideIn {
  0% {
    right: 100%;
    opacity: 0;
  }
  100% {
    right: 0%;
    opacity: 1;
  }
}
.clear {
  width: 100%;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}
.clear i {
  font-size: 2rem;
  color: #e7e7eb;
}
form {
  width: 100%;
  height: auto;
  display: -webkit-flex;
  display: flex;
  margin: 1rem 0px;
}
.input-control {
  -webkit-flex-grow: 0.7;
          flex-grow: 0.7;
  position: relative;
  margin-right: 0.75rem;
}
.input-control i {
  position: absolute;
  color: #616475;
  left: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 1;
}
.input-control input {
  width: 100%;
  color: #e7e7eb;
  background: none;
  font-size: 1rem;
  padding: 15px 15px 15px 50px;
  border: 1.5px solid #e7e7eb;
}
.input-control input::-webkit-input-placeholder {
  color: #616475;
}
.input-control input:-ms-input-placeholder {
  color: #616475;
}
.input-control input::placeholder {
