}
.input-control input:focus {
  outline: none !important;
  border-color: #f2f2f2;
}
.search-submit {
  padding: 15px;
  -webkit-flex-grow: 0.3;
          flex-grow: 0.3;
  color: #e7e7eb;
  font-size: 1rem;
  font-weight: 600;
  border: 1.5px solid #3c47e9;
  background: #3c47e9;
  cursor: pointer;
}
.search-submit:focus {
  outline: none !important;
  border-color: #616475;
}

.result-search {
  margin: 1.5rem 0px;
  width: 100%;
  overflow-y: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.result-button {
  display: -webkit-flex;
